import React, {useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';


function CategoryDetail({ match }) {

    const [category, setCategory] = useState({})
    const [posts, setPosts] = useState([])
    const id = match.params.id

    useEffect(() => {
        axios({
            method: "GET",
            url: `http://127.0.0.1:8080/api/category/${id}/`
        }).then(response => {
            setCategory(response.data)
            setPosts(response.data.posts)
        })
    }, [id])

    return (
        <div>
            Category with id {category.id}
            <p>Category <strong>{category.name}</strong></p>
            <hr />
            <div className="row">
                {posts.map(p => (
                    <div className="col-md-4" key={p.id}>
                        <div class="d-grid gap-2 col-6 mx-auto">
                            <h4>{p.title}</h4>
                            <p>{p.content}</p>
                            <p>{p.id}</p>
                            <img src={{ pathname:`${p.image.url}`, fromDashboard: false }}  alt="image" width="250" height="250"/>
                        </div>
                    <Link to={{ pathname: `/posts/${p.id}`, fromDashboard: false }}>
                        <div class="d-grid gap-2 col-6 mx-auto">
                            <button class="btn btn-primary" type="button">Детали</button>
                        </div>
                    </Link>
            </div>
                ))}
        </div>
    </div>
    );
}


export default CategoryDetail;